import axios, {isAxiosError} from "axios";

export type UserDTO = {
    id: number
    firstname: string
    lastname: string
    profilePhoto: string
    country: string
}

export const me = async () => {
    const meEndpoint = `${process.env.REACT_APP_BASE_URL || ''}/auth/me`
    try {
        const response = await axios.get<UserDTO>(meEndpoint, {withCredentials: true})
        return response.data
    } catch (error: unknown) {
        if (isAxiosError(error) && error.response) {
            if (error.response.status === 401) {
                return undefined
            } else {
                throw error
            }
        } else {
            throw error
        }
    }
}

export const logout = async () => {
    const logoutEndpoint = `${process.env.REACT_APP_BASE_URL || ''}/auth/logout`
    return axios.get(logoutEndpoint, {withCredentials: true})

}