import {
    Accordion as AccordionChakra,
    AccordionButton,
    AccordionIcon,
    AccordionItem,
    AccordionPanel,
    Box, createMultiStyleConfigHelpers
} from "@chakra-ui/react";
import {accordionAnatomy} from "@chakra-ui/anatomy"
import React from "react";

const { definePartsStyle, defineMultiStyleConfig } =
    createMultiStyleConfigHelpers(accordionAnatomy.keys)

const custom = definePartsStyle({
    root: {
        color: 'swords.black'
    },
    container: {
        border: 'none'
    },
    button: {
        bg: 'swords.white',
        borderStyle: 'solid',
        borderWidth: '1px',
        borderColor: 'swords.black',
        _hover: {
            bg: 'swords.white',
        }
    },
})

export const accordionTheme = defineMultiStyleConfig({
    variants: { custom },
})

export interface AccordionEntry {
    heading: string
    content: string
}

export interface AccordionProps {
    entries: AccordionEntry[]
}

export const Accordion: React.FC<AccordionProps> = ({entries}) => {
    return (
        <AccordionChakra w={['full', '470px']} variant='custom'>
            {entries.map(({heading, content}, index) => (
                <AccordionItem my={4} key={index}>
                    <h2>
                        <AccordionButton>
                            <Box as="span" flex='1' textAlign='left'>
                                {heading}
                            </Box>
                            <AccordionIcon />
                        </AccordionButton>
                    </h2>
                    <AccordionPanel pt={8}>
                        {content}
                    </AccordionPanel>
                </AccordionItem>
            ))}
        </AccordionChakra>
    )
}
