import React, {createContext, ReactNode, useCallback, useContext, useLayoutEffect, useMemo, useState} from "react";
import {logout as apiLogout, me as apiMe, UserDTO} from "../api/auth";

const useSession = () => {
    const [authenticatedUser, setAuthenticatedUser] = useState<UserDTO | undefined>()
    const [error, setError] = useState<unknown>()

    const login = useCallback(() => {
        const loginEndpoint = `${process.env.REACT_APP_BASE_URL || ''}/auth/login`
        window.location.assign(loginEndpoint)
    }, [])

    const logout = useCallback(async () => {
        try {
            await apiLogout()
            setAuthenticatedUser(undefined)
        } catch (e: unknown) {
            setError(e)
        }
    }, [])

    const me = useCallback(async () => {
        try {
            const user = await apiMe()
            setAuthenticatedUser(user)
        } catch (e: unknown) {
            setError(e)
        }
    }, [])

    useLayoutEffect(() => {
        me()
    }, [me])

    const isAuthenticated = Boolean(authenticatedUser)
    return useMemo(() => ({
        user: authenticatedUser,
        error,
        isAuthenticated,
        login,
        logout
    }), [authenticatedUser, error, isAuthenticated, login, logout])
}

type AuthContextValue = ReturnType<typeof useSession>

export const AuthContext = createContext<AuthContextValue | undefined>(undefined)

type AuthProviderProps = {
    children: ReactNode
}

export const AuthProvider: React.FC<AuthProviderProps> = (props) => {
    const {children} = props
    const auth = useSession()
    return (
        <AuthContext.Provider value={auth}>
            {children}
        </AuthContext.Provider>
    )
}

export const useAuth = () => {
    const ctx = useContext(AuthContext)

    if (!ctx) {
        throw new Error(`Usage outside of context.`)
    }

    return ctx
}