import {Box, Center, Flex, SimpleGrid} from "@chakra-ui/react";
import React from "react";

interface ListItemProps {
    heading: string
    subheading?: string
    index: number
}

const ListItem: React.FC<ListItemProps> = ({heading, subheading, index}) => {
    return (
        <Flex flexDirection='column' alignItems='center' justifyContent='center' color='swords.black'>
            <Center textStyle='subheading' borderRadius='50%' h='40px' w='40px' bg='swords.red' color='swords.beige'>{index}</Center>
            <Box textStyle='subheading'>{heading}</Box>
            <Box textStyle='bulletText'>{subheading}</Box>
        </Flex>
    )
}

export type Entry = Pick<ListItemProps, 'heading' | 'subheading'>

interface ListProps {
    entries: Entry[]
}

export const List: React.FC<ListProps> = ({entries}) => {
    return (
        <SimpleGrid columns={1} spacingY={['40px', '48px']}>
            {entries.map(({heading, subheading}, index) => (
                <ListItem heading={heading} subheading={subheading} index={index} key={index} />
            ))}
        </SimpleGrid>
    )
}
